<template>
  <div id="index">
    <Row>
      <Col span="12" class="header">
        <Card :bordered="false" :padding=0>
          <img src="../assets/header_bg.jpg">
          <div class="title">
            <h1>{{sys.name}}</h1>
            <time>{{today}}</time>
          </div>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="countdown">
        <h1>倒计时</h1>
        <p>
          大獎池24小時開壹次獎，每有壹筆新投入，時間將按照1个以太延長3小時计算，獎池100%的獎金將給予最後500名投資額最大玩家。小獎池24小時開壹次獎，100%開出，壹等獎1名直接奪得奖池的1/3，二等獎10名平分奖池的1/3，三等獎20名平分奖池的1/3。</p>
        <div class="big">
          <img src="../assets/eth.png" alt="">
          <p>大奖池</p>
          <time>{{bigTime}}</time>
          <span>{{contractInfo.bigJackpot}} ETH</span>
        </div>
        <div class="small">
          <img src="../assets/eth.png" alt="">
          <p>小奖池</p>
          <time>{{smallTime}}</time>
          <span >{{contractInfo.smallJackpot}} ETH</span>
        </div>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="involvement">
        <Divider class="title">我要参与</Divider>
        <Card class="card" :padding=0 :bordered="false">
          <Tabs class="tabs">
            <TabPane label="参与">
              <div class="inPlan">
                <p>当前燃烧比例 <span>1ETH = {{contractInfo.consume}}TTS</span></p>
                <div class="btnGroup">
                  <ButtonGroup>
                    <Button ghost @click="inPlanAmount=1">1~10ETH</Button>
                    <Button ghost @click="inPlanAmount=11">11~30ETH</Button>
                    <Button ghost @click="inPlanAmount=31">31ETH+</Button>
                  </ButtonGroup>
                </div>
                <Input class="input" v-model.number="inPlanAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyPlan">参与</Button>
              </div>
            </TabPane>
            <TabPane label="门票">
              <div class="ticket">
                <p>购买入场门票 <span>1ETH = {{contractInfo.rate}}TTS</span></p>
                <Input class="input" v-model.number="rateAmount"><span slot="append">ETH</span></Input>
                <Button type="success" long @click="buyRate">购买</Button>
              </div>
            </TabPane>
            <TabPane label="邀请">
              <div class="inTeam">
                <p>我的推薦人是: {{user&&user.sup?user.sup.addr:'--'}}</p>
                <p>我的直推數量: {{user&&user.sup?user.sub.length:'--'}}</p>
                <p>我的邀請鏈接: {{user.addr||'--'}}</p>
                <Button type="success" long @click="doCopy">复制我的邀请链接</Button>
              </div>
            </TabPane>
            <TabPane label="规则">
              <ul class="rule">
                <li>
                  <Icon type="ios-star"/>
                  壹個賬號在出局前僅接受壹筆投資，出局後可繼續投資
                </li>
                <li>
                  <Icon type="ios-star"/>
                  靜態分紅按照每天0.3%~2.2%上下浮動
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動靜出局，按照1-10為投資額3倍，11-30為投資額4倍，大於31為投資額5倍
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態推薦25%按照:10%->8%->7%獲取動態獎勵，可即時提現
                </li>
                <li>
                  <Icon type="ios-star"/>
                  小獎池達到指定時間後，小獎池不再沉澱，直到開啟下壹輪
                </li>
                <li>
                  <Icon type="ios-star"/>
                  大獎池達到指定時間後，不再沉澱，投資額直接進入分紅收益
                </li>
                <li>
                  <Icon type="ios-star"/>
                  動態如果未達到條件，沉澱至分紅池
                </li>
              </ul>
            </TabPane>
          </Tabs>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="logs">
        <Divider class="title">统计数据</Divider>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p><span class="label">已結算分紅收益</span> <span class="extra">-- ETH</span></p>
          <p><span class="label">奖池收益</span> <span class="extra">{{user.jackpot |flex(4)}} ETH</span></p>
          <p><span class="label">下次分红收益时间差</span> <span class="extra">{{smallTime}} ETH</span></p>
          <p><span class="label">动静出局还差</span> <span class="extra">{{user| outNum | flex(4)}} ETH</span></p>
          <p><span class="label">3倍未结算预计收益</span> <span class="extra">{{user.static3 | flex(4)}} ETH <Button type="success" size="small">结算</Button></span></p>
          <p><span class="label">4倍未结算预计收益</span> <span class="extra">{{user.static4 | flex(4)}} ETH <Button type="success" size="small">结算</Button></span></p>
          <p><span class="label">5倍未结算预计收益</span> <span class="extra">{{user.static5 | flex(4)}} ETH <Button type="success" size="small">结算</Button></span></p>
          <p><span class="label">未提币数量</span> <span class="extra">{{user.daysAmount | flex(4)}} ETH </span></p>
        </Card>
        <Card class="card infoList " :padding=0 :bordered="false">
          <p><span class="label">大奖池当前总量</span> <span class="extra">{{contractInfo.bigJackpot | flex(4)}} ETH </span></p>
          <p><span class="label">小奖池当前总量</span> <span class="extra">{{contractInfo.smallJackpot | flex(4)}} ETH </span></p>
          <p><span class="label">门票焚毁倍率</span> <span class="extra">{{contractInfo.consume}} ETH </span></p>
          <p><span class="label">已焚毁门票</span> <span class="extra">{{contractInfo.totalConsume}} ETH </span></p>
          <p><span class="label">已投入的总资金</span> <span class="extra">{{user.invest | flex(4)}} ETH </span></p>
          <p><span class="label">团队</span> <span class="extra">{{user.dynamic | flex(4)}} ETH <Button type="success" size="small">结算</Button></span></p>
          <p><span class="label">大使</span> <span class="extra">{{user.angelAmount | flex(4)}} ETH <Button type="success" size="small">结算</Button></span></p>
        </Card>
        <Card class="card jcList" :padding=0 :bordered="false">
          <p class="big">大奖池</p>
          <CellGroup>
            <Cell title="No.1" extra="-- ETH"/>
          </CellGroup>
          <p class="small">小奖池</p>
          <CellGroup>
            <Cell title="No.1" extra="-- ETH"/>
          </CellGroup>
        </Card>
      </Col>
    </Row>
  </div>

</template>

<script>
    import mixins from '../mixins'
    import moment from 'moment';
    import Bn from 'bignumber.js'
    import * as api from '../api'
    import * as types from '../store/mutation-types'
    export default {
        name: "index",
        mixins: [mixins],
        data() {
            return {
                msg: [],
                bigTime: null,
                smallTime: null,
                today: moment().format('YYYY.MM.DD'),
                inPlanAmount: 0,
                rateAmount: 0,
            }
        },
        filters:{
            flex(e,r){
                if(e){
                    return e.toFixed(4)
                }else{
                    return e;
                }
            },
            outNum(e){
                let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
                let c = [e.investGroup[0]*3,e.investGroup[1]*4,e.investGroup[2]];
                let s = new Bn(c[0]).plus(c[1]).plus(c[2]).toNumber();
                return s-n;
            }

        },
        computed: {
            url() {
                if (this.user) {
                    return `${this.sys.url}/?sup=${this.user.addr}`
                } else {
                    return `${this.sys.url}/`
                }
            },

        },
        mounted() {
            // 初始化
            this.countTime();
            let that = this;
            this.sockets.subscribe('reload', data => that.init());
        },
        methods: {
            // 初始化
            init() {
                let that = this;
                this.$store.dispatch('initService');
                this.countTime();
                setTimeout(function () {
                    that.$store.dispatch('newContract');
                }, 5000)
            },
            // 复制邀请码
            doCopy() {
                if (!this.user||!this.user._id) return this.$Message.error('您还不是本站会员！');
                if (this.account) this.$copyText(this.url).then(e => this.$Message.success('复制成功！'))
            },
            getMessage(msg) {
                this.$store.dispatch('newContract');
                let data = JSON.parse(msg.data);
                if (data.user) this.$store.commit(types.UPDATE_USER, data.user);
                if (data.sys) this.$store.commit(types.UPDATE_SYSTEM, data.sys);
                this.countTime();
            },
            // 倒计时
            countTime: function () {
                let bTime = new Date(this.sys.bigTime).getTime() - new Date().getTime();
                let sTime = new Date(this.sys.smallTime).getTime() - new Date().getTime();
                if (bTime >= 0) this.bigTime = Math.floor(bTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(bTime / 1000 / 60 % 60) > 9 ? Math.floor(bTime / 1000 / 60 % 60) : '0' + Math.floor(bTime / 1000 / 60 % 60)) + ":" + (Math.floor(bTime / 1000 % 60) > 9 ? Math.floor(bTime / 1000 % 60) : '0' + Math.floor(bTime / 1000 % 60));
                if (sTime >= 0) this.smallTime = Math.floor(sTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(sTime / 1000 / 60 % 60) > 9 ? Math.floor(sTime / 1000 / 60 % 60) : '0' + Math.floor(sTime / 1000 / 60 % 60)) + ":" + (Math.floor(sTime / 1000 % 60) > 9 ? Math.floor(sTime / 1000 % 60) : '0' + Math.floor(sTime / 1000 % 60));
                setTimeout(this.countTime, 1000);
            },
            // 购买门票
            buyRate() {
                if (!this.user||!this.user._id) return;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.rateAmount) || this.rateAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.tickets().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.rateAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        that.$store.commit(types.LOADDING, true);
                        if (hash) await api.saveHash({hash, type: 1, amount: that.rateAmount});
                        hashs = hash;
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 1, amount: that.rateAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        that.rateAmount = 0;
                        if (receipt.status) that.$Message.success('购买成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
            // 参与计划
            buyPlan() {
                if (!this.user||!this.user._id) return;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.inPlanAmount) || this.inPlanAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.ticketConsume().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.inPlanAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        if (hash) await api.saveHash({hash, type: 2, amount: that.inPlanAmount});
                        hashs = hash;
                        that.$store.commit(types.LOADDING, true);
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 2, amount: that.inPlanAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        if (receipt.status) that.$Message.success('参与成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
        }
    }
</script>

<style lang="less">
  #index {
    .title {
      color: #008c7c;
      padding: 0 3rem;

      span {
        font-size: 1.4rem;
      }
    }

    .header {
      position: relative;
      width: 100%;

      img {
        width: 100%;
        float: left;
      }

      .title {
        text-align: center;
        position: absolute;
        margin-top: 14%;
        top: 0;
        left: 0;
        right: 0;
        color: #ffffff;

        h1, time {
          text-shadow: 3px 3px 4px #000000;
        }

        h1 {
          font-size: 3rem;
          font-weight: 400;
        }

        time {
          margin-top: 1rem;
          border-top: 1px solid #ffffff;
          border-bottom: 1px solid #ffffff;
          width: 16rem;
          display: inline-block;
          padding: .5rem 0;
          font-size: 1.2rem;
        }
      }

      img {
        margin: 0;
      }
    }

    .countdown {
      width: 100%;
      background: rgba(19, 7, 104, .3);
      overflow: hidden;
      padding: 1rem;
      text-align: center;

      & > h1 {
        font-size: 2.4rem;
        text-align: center;
        margin: 2rem;
      }

      & > p {
        text-indent: 2rem;
        line-height: 1.5rem;
        font-size: .9rem;
        text-align: left;
      }

      img {
        position: absolute;
        left: 8%;
        z-index: 1;
        opacity: 1;
        top: 40%;
        height: 4rem;
      }

      .big {
        color: #c5085b;
        border: 1px solid #C5085B;
      }

      .small {
        color: #bcbc37;
        border: 1px solid #BCBC37;
        margin-bottom: 2rem;
      }

      & > div {
        position: relative;
        width: 80%;
        display: inline-block;
        padding: 10px;
        margin-top: 3rem;
        border-radius: 7px;

        time, span {
          position: relative;
          display: block;
          z-index: 2;
        }

        p {
          font-size: 1.2rem;
        }

        time {
          font-size: 2.4rem;
        }

        span {
          font-size: 2rem;
          line-height: 2rem;
        }
      }
    }

    .involvement {
      width: 100%;

      .card {
        width: 90%;
        margin: auto;
        padding: 0;
        box-shadow: 0 0 10px 3px #00D7BD;
        overflow: hidden;
        background: #2a2a2a;
      }

      .tabs {
        .ivu-tabs-nav-scroll {
          background: #000000;

          .ivu-tabs-nav {
            width: 100%;

            .ivu-tabs-ink-bar {
              background-color: #CA0000;
            }

            .ivu-tabs-tab {
              text-align: center;
              color: #ffffff;
            }

            .ivu-tabs-tab-active {
              color: #CA0000;
            }
          }
        }

        .ivu-tabs-content {
          .inPlan, .inTeam, .ticket, .rule {
            padding: 0 20px 20px;
            color: #ffffff;

            p {
              line-height: 2rem;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
              span {
                color: #ca0000;
              }
            }
          }

          .inPlan, .ticket {
            p {
              font-weight: 500;
              margin-bottom: 1rem;
              font-size: 1.1rem;

              span {
                font-size: 1.1rem;
              }
            }
          }

          .inPlan, .ticket {
            .btnGroup {
              text-align: center;
              margin-bottom: 1rem;

              .ivu-btn-group {
                border-radius: 4px;
                box-shadow: 0 0 4px 1px #00D7BD;
              }

              button {
                border: 1px solid #00D7BD;
                color: #00d7bd;
                text-shadow: 1px 1px 2px #00D7BD;
              }
            }

            .input {
              margin-bottom: 1rem;
              height: 2.6rem;
              line-height: 2.6rem;

              input {
                text-indent: 1rem;
                height: 2.6rem;
              }
            }
          }

          .rule {
            list-style: none;

            i {
              color: #ffc500;
            }
          }
        }
      }
    }

    .logs {
      width: 100%;

      .card {
        border-radius: 0;
        background: rgba(0, 102, 160, .3);

        .ivu-cell + .ivu-cell {
          border-top: 1px solid #008c7c;
        }

        & + .card {
          margin-top: 10px;
        }

        div {
          color: #008c7c;
        }

        .btns .ivu-cell-main {
          width: 100%;
        }
      }
      .infoList{
        p{
          padding: 0 1rem;
          line-height: 40px;
          .extra{
            float: right;
          }
          &+p{
            border-top: 1px solid #008c7c;
          }
        }
      }
      .jcList {
        .big {
          color: #c5085b;
        }

        .small {
          color: #f7b500;
        }

        p {
          text-align: center;
          line-height: 3rem;
          border-bottom: 1px solid #008c7c;
        }
      }
    }
  }
</style>



